a{text-decoration:none}.card_container{display:flex;align-items:center;justify-content:center;position:relative;margin:20px auto;width:200px;height:150px;background:#fff;border-radius:20px 20px 10px 10px;font-size:20px;font-weight:lighter;margin-bottom:100px;margin-left:60px;-moz-user-select:none;-website-user-select:none;-webkit-user-select:none;-ms-user-select:none;user-select:none;box-shadow:30px 36px 48px -10px rgba(80,102,99,.7);transition:all .3s cubic-bezier(0.25, 0.8, 0.25, 1)}.card_container:hover{box-shadow:0 5px 35px 0px rgba(0,0,0,.1);transform:translateY(-1px) scale(1.1) translateZ(0)}.card_container:hover::before,.card_container:hover::after{display:block;content:"";position:absolute;width:200px;height:150px;border-radius:20px 20px 10px 10px;z-index:-1;-webkit-animation:6s glow infinite;animation:6s glow infinite}@-webkit-keyframes glow{0%{box-shadow:30px 36px 48px -12px rgba(80,102,99,.7)}25%{box-shadow:26px 30px 50px -23px #506663}50%{box-shadow:30px 36px 48px -12px #506663}75%{box-shadow:26px 30px 50px -23px #506663}100%{box-shadow:30px 36px 48px -12px #506663}}@keyframes glow{0%{box-shadow:30px 36px 48px -12px rgba(80,102,99,.7)}25%{box-shadow:26px 30px 50px -23px #506663}50%{box-shadow:30px 36px 48px -12px #506663}75%{box-shadow:26px 30px 50px -23px #506663}100%{box-shadow:30px 36px 48px -12px #506663}}.card_img_container{position:absolute;width:100px;height:100px}.loading_gif{position:relative;height:100%;top:-200px}.card_pokemon_img{position:relative;height:250%;left:50px;top:-20px;transform:translate(-50%, -50%);transition:all .3s ease-out}.card_container:hover .pokemon_name_card{color:#4b4b4b;font-size:1.3em;transition:all .3s ease-out;transform:translateY(-1px) scale(1.005) translateZ(0);z-index:-1}.card_container:hover .card_pokemon_img{width:300px;height:auto;transition:all .3s ease-out}.pokemon_name_card{text-align:center;font-size:1.5rem;color:#4b4b4b;-webkit-text-stroke-width:.5px;-webkit-text-stroke-color:#fff;margin-top:160px;margin-left:50px}.pokemon_id{-webkit-text-stroke-width:.5px;-webkit-text-stroke-color:#fff;position:absolute;left:10px;bottom:-30px;font-size:1.5rem;color:#4b4b4b}.index_tag{-webkit-text-stroke-width:.5px;-webkit-text-stroke-color:#fff;position:absolute;left:10px;bottom:-20px;font-size:1rem;color:#4b4b4b}
.all_cards{display:flex;flex-wrap:wrap;justify-content:space-around;margin:250px}.search-label{position:-webkit-sticky;position:sticky;top:90px;z-index:100}
.card{border-radius:20px;position:relative;height:450px;width:900px;margin-top:12%;margin-left:25%;margin-right:25%;background-color:#fff;box-shadow:10px 10px 93px 0px rgba(0,0,0,.75)}.thumbnail{float:left;position:relative;left:10px;top:-50px;height:520px;width:630px;object-fit:cover;object-position:center}.pokemon_image_big{position:relative;left:50%;top:50%;height:110%;width:auto;transform:translate(-50%, -50%)}.right{margin-left:600px;margin-right:20px}.pokemon_name_info{font-weight:500;text-transform:uppercase;font-size:2.4em;color:#4b4b4b;text-align:left;-webkit-margin-after:10px;margin-block-end:10px;-webkit-margin-before:20px;margin-block-start:20px;display:inline-block}.type{display:flex}.pokemon_type{font-weight:400;margin-top:0;min-height:30px;width:calc(50% - 15px);text-align:center;border-radius:15px 0 15px 0;color:#fff}.separator{margin-left:10px;border:1px solid #c3c3c3}.pokemon_description{max-height:150px;font-size:1rem;line-height:130%;color:#4b4b4b;margin-bottom:20px}.icon_container{display:flex;justify-content:center;align-items:center;color:gray;margin-top:40px}.weight_info{margin:5px auto;font-size:1em}.height_info{margin:5px auto;font-size:1em}.pokemon_id_num{font-weight:400;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:#fff;position:absolute;left:30px;bottom:-120px;font-size:6rem;color:#4b4b4b}.pokemon_number{font-weight:bold;-webkit-text-stroke-width:.5px;-webkit-text-stroke-color:#fff;position:absolute;left:30px;top:-50px;font-size:2rem;color:#4b4b4b}.card:hover .stats_card{display:block;-webkit-animation:slide-left .5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;animation:slide-left .5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both}@-webkit-keyframes slide-left{0%{transform:translateX(0)}100%{transform:translateX(-100px)}}@keyframes slide-left{0%{transform:translateX(0)}100%{transform:translateX(-100px)}}.arrow{position:absolute;top:45%;width:50px;height:50px;background:transparent;border-top:5px solid #4b4b4b;border-right:5px solid #4b4b4b;box-shadow:0 5px -5px #d3d3d3;transition:all 200ms ease}.arrow.arrow_left{left:-150px;transform:translate3d(0, -50%, 0) rotate(-135deg)}.arrow.arrow_right{right:-150px;transform:translate3d(0, -50%, 0) rotate(45deg)}.arrow:hover{border-color:#9547ef;box-shadow:5px -5px 0 #4b4b4b}
.stats_card{border-radius:20px;position:absolute;height:330px;width:300px;padding:10px;right:-150px;top:-50px;background-color:rgba(255,255,255,.95);display:none;box-shadow:0px 10px 13px 7px rgba(0,0,0,.75)}.container_stat{width:257px;margin-left:20px;color:#4b4b4b}.stat_name{font-size:20px;font-weight:400;margin-bottom:0px;margin-top:10px}.progress-bar{height:15px;background-image:linear-gradient(to bottom, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.05));transition:.1s linear;transition-property:width,background-color}.progress{margin-top:5px;background:rgba(0,0,0,.25);box-shadow:inset 0 2px 2px #000,0 1px #000;-webkit-box-shadow:0 2px 2px #000,0 1px #000;-moz-box-shadow:0 2px 2px #000,0 1px #000}.progress-moved .progress-bar{background-color:#eb7591;-webkit-animation:progress1Animation 2s;animation:progress1Animation 2s}.progress-moved1 .progress-bar{background-color:#1d1b1c;-webkit-animation:progress1Animation 2s;animation:progress1Animation 2s}.progress-moved2 .progress-bar{background-color:#a6d6d6;-webkit-animation:progress1Animation 2s;animation:progress1Animation 2s}.progress-moved3 .progress-bar{background-color:#dfe988;-webkit-animation:progress1Animation 2s;animation:progress1Animation 2s}.progress-moved4 .progress-bar{background-color:#c09de9;-webkit-animation:progress1Animation 2s;animation:progress1Animation 2s}.progress-moved5 .progress-bar{background-color:#a7e0ab;-webkit-animation:progress1Animation 2s;animation:progress1Animation 2s}@-webkit-keyframes progress1Animation{0%{width:5%;background-color:#f9bcca}25%{width:25%;background-color:#f9bcca}50%{width:100%;background-color:#f9bcca}100%{width:25%;background-color:#f9bcca}}@keyframes progress1Animation{0%{width:5%;background-color:#f9bcca}25%{width:25%;background-color:#f9bcca}50%{width:100%;background-color:#f9bcca}100%{width:25%;background-color:#f9bcca}}
.abilities_info_card{border-radius:10px;position:absolute;height:50px;width:auto;top:10px;left:-100px;background-color:rgba(255,255,255,.95);box-shadow:0px 10px 13px 7px rgba(0,0,0,.75);z-index:3;display:none}.evs_info_card{display:none;border-radius:10px;position:absolute;height:50px;width:auto;top:100px;left:-100px;background-color:rgba(255,255,255,.95);box-shadow:0px 10px 13px 7px rgba(0,0,0,.75);z-index:3}.egg_info_card{border-radius:10px;position:absolute;height:50px;width:100px;top:200px;left:-100px;background-color:rgba(255,255,255,.95);box-shadow:0px 10px 13px 7px rgba(0,0,0,.75);z-index:3;display:none}.evs_tag{font-size:20px;font-weight:400;margin-bottom:0px;margin-top:10px;color:#555;display:none}.abilities_tag{font-size:20px;font-weight:400;margin-bottom:0px;margin-top:10px;color:#555;text-align:center;display:none}.egg_tag{font-size:20px;font-weight:400;margin-bottom:0px;margin-top:10px;color:#555;text-align:center;display:none}
